import "./styles.css";

const onclickAdd = () => {
  //å–å¾—
  const inputText = document.getElementById("add-text").value;
  //åˆæœŸåŒ–
  document.getElementById("add-text").value = "";
  createImcompleteList(inputText);
};

//æœªå®Œäº†ã‹ã‚‰å‰Šé™¤ã™ã‚‹å…±é€šã‚¤ãƒ™ãƒ³ãƒˆ
const deleteFromIncompleteList = (target) => {
  document.getElementById("incomplete-list").removeChild(target);
};

//æœªå®Œäº†ãƒªã‚¹ãƒˆã‚’ä½œæˆã™ã‚‹å…±é€šã‚¤ãƒ™ãƒ³ãƒˆ
const createImcompleteList = (text) => {
  //divç”Ÿæˆ
  const div = document.createElement("div");
  div.className = "list-row";
  //liç”Ÿæˆ
  const li = document.createElement("li");
  li.innerText = text;
  //ulã«è¿½åŠ 
  document.getElementById("incomplete-list").appendChild(div);

  //å®Œäº†ãƒœã‚¿ãƒ³ç”Ÿæˆ
  const completeButton = document.createElement("button");
  completeButton.innerText = "å®Œäº†";
  completeButton.addEventListener("click", () => {
    //liç”Ÿæˆ
    const compli = completeButton.parentNode.firstChild;
    const backButton = document.createElement("button");
    //æˆ»ã™ãƒœã‚¿ãƒ³ã‚¤ãƒ™ãƒ³ãƒˆ
    backButton.innerText = "æˆ»ã™";
    backButton.addEventListener("click", () => {
      const backTarget = backButton.parentNode;
      document.getElementById("complete-list").removeChild(backTarget);
      const text = backTarget.firstElementChild.innerText;
      createImcompleteList(text);
    });
    //è¿½åŠ ã™ã‚‹Div
    const compdiv = document.createElement("div");
    compdiv.className = "list-row";
    compdiv.appendChild(compli);
    compdiv.appendChild(backButton);

    //å®Œäº†ã‚¨ãƒªã‚¢ã«è¿½åŠ 
    document.getElementById("complete-list").appendChild(compdiv);
    //å‰Šé™¤
    deleteFromIncompleteList(completeButton.parentNode);
  });

  //å‰Šé™¤ãƒœã‚¿ãƒ³ç”Ÿæˆ
  const deleteButton = document.createElement("button");
  deleteButton.innerText = "å‰Šé™¤";
  deleteButton.addEventListener("click", () => {
    //å‰Šé™¤
    deleteFromIncompleteList(deleteButton.parentNode);
  });

  //è¡Œç”Ÿæˆ
  div.appendChild(li);
  div.appendChild(completeButton);
  div.appendChild(deleteButton);
};

//è¿½åŠ ãƒœã‚¿ãƒ³
document
  .getElementById("add-button")
  .addEventListener("click", () => onclickAdd());
